import React from "react";
import "./SelectLatestBom.css";
import { getLastUsedRowNumber, getactiveSheetName, writeMaterilToExcel } from "../../../constFunctions/excelFunction";
import { useDispatch, useSelector } from "react-redux";
import { setSingleMaterialData } from "../../../redux-toolkit/slice/action";

function SelectLatestBom({ bomData, setAssociatedBom }) {
  const { bomMappingData } = useSelector((state) => state.taskpane);
  const { singleMaterialData } = useSelector((state) => state.action);
  const dispatch = useDispatch();
  const writeAssBomMaterialToExcel = async (id) => {
    const lastUsedRow = await getLastUsedRowNumber();
    const sheetName = await getactiveSheetName();
    const materialData = { ...singleMaterialData };
    materialData.associated_product_code_bom = id;
    writeMaterilToExcel(sheetName, lastUsedRow + 1, materialData, bomMappingData);
    setAssociatedBom(null);
  };
  return (
    <div className="select-latest-bom-container">
      {bomData?.map((element, index) => (
        <div
          key={index}
          className="subBomNames"
          onClick={() => {
            dispatch(setSingleMaterialData(element?.bom_versions[0]?.id));
            writeAssBomMaterialToExcel(element?.bom_versions[0]?.id);
          }}
        >
          {element?.name}
        </div>
      ))}
    </div>
  );
}

export default SelectLatestBom;
